import React, { Component } from "react";
import data from "../data.json"
import Cart from "./Cart.js";

import { Button, Card, Form} from "react-bootstrap";

localStorage.setItem('data', JSON.stringify(data.Products))

class ProductItem extends Component{
    constructor(props){
        super(props);
        this.state={
            count:0
        }
    this.onSubmit = this.onSubmit.bind(this);

    }

  

    onSubmit(event){
        const {name,logo,price}=this.props;
        event.preventDefault();
        this.props.onAdd(name,logo,price);
        console.log(this.props.onAdd)
        var count=this.state.count + 1
        this.setState({count:count});
        }


    render() {
    const {name,logo,description,price}=this.props;
    var count=this.props.count;
    var product=this.props.prod;

    return(

                        <div key={name}>
                            
                            <Card className='column'>
                            <Card.Img className='img_coffee' variant="top" src={logo} />
                            <Card.Body>
                              <Card.Title> {name} </Card.Title>
                              <Card.Text>{description} </Card.Text>
                            </Card.Body>
                            <Card.Footer> {price} </Card.Footer>
                            <Card.Footer>     
                            <Form onSubmit={this.onSubmit}>                     
                            <Button variant="dark"  > Add To Cart </Button>
                            </Form>  
                             </Card.Footer>
                          </Card>

                          </div>
                    
    )
}
}
export default ProductItem