import React, {Component} from 'react';
import { Button} from "react-bootstrap";
import ListItem from '@material-ui/core/ListItem';
import Paper from '@material-ui/core/Paper';
import List from "@material-ui/core/List";
import CartItem from './CartItem'

class Cart extends Component {
 
  constructor(props) {
    super(props)
    this.state = { 
      cart_products:[],
    }
    this.onAdd = this.onAdd.bind(this);
    this.onDelete = this.onDelete.bind(this);
  }

  getProducts(){
    return this.state.cart_products;
    }

    componentWillMount(){
        const products= this.getProducts()
        this.setState({cart_products: products});

    }

  onDelete(name){
    const products= this.getProducts()
    const filtered_cart= products.filter( product =>
      {return product.name !== name;
      })
      this.setState({cart_products: filtered_cart});
 }

  onAdd(name,logo,price){
    const add_products= this.getProducts()
    console.log("befor add",add_products)
    add_products.push({
      name,
      price,
      logo
    })
    console.log("after add",add_products)
    this.setState({cart_products: add_products});

     
 }

  render(){

 if (! this.state.cart_products.length == 0) {
  return (   <div> <h1> Your cart is empty ! </h1>  
  <Button variant='dark' href='/'> Shop Now </Button>
  </div>
  )
}

  else {
    return (   
  <div>
    <div >
      <h1> Shopping Bag </h1>
    </div>
    
  <div >
      <Paper >
      <List>
      {   
                this.state.cart_products.map( product => {
                    return(
            <ListItem>
            <CartItem 
            name={product.coffeeName}
            price={product.price}
            logo={product.logo}
            onDelete={this.onDelete}
            />
              </ListItem>
                     )} )
                   
    }
      </List>
      </Paper>
    </div>
 


    </div> 
  );
    }
}
}

export default Cart;