import React, { Component } from "react";
import { Nav, Navbar,  Badge, NavDropdown } from 'react-bootstrap'
import { Form, Button,  FormControl} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import logo from '..//images/logo.png'


 class Menu extends Component{
     render(){
         
    return(
      <div>

      <Navbar bg="dark" variant="dark" className='nav'>

        <Navbar.Brand >
          <img
            alt="logo"
            src={logo}
            width="40"
            height="40"
          />{' '}
          Coffee Shop
        </Navbar.Brand>     

        <Navbar.Collapse>

              <Nav className="mr-auto">

              <Nav.Link href="/about"> About </Nav.Link>
              <Nav.Link href="/contact"> Contact Us </Nav.Link>
              <Nav.Link href="/cart">  My Shopping Bag </Nav.Link>

              </Nav>
              
              <i className="fas fa-user"> </i>

            <Nav> 
                <NavDropdown title="" id="basic-nav-dropdown"> 
                <NavDropdown.Item href="/register">Register</NavDropdown.Item>
                <NavDropdown.Item href="/login">Login</NavDropdown.Item>
              </NavDropdown>

                <Nav.Link href="/cart"> <i className="fas fa-shopping-cart"> </i>
                <Badge pill variant="danger" className='badge'>1</Badge>
                </Nav.Link>
           </Nav>

      <div>
           <Form inline>
            <FormControl type="text" placeholder="Search..." className="mr-sm-2" />
            <Button variant="outline-light"> <i className="fas fa-search"></i> </Button>
          </Form>
        </div>
        </Navbar.Collapse>
      </Navbar>

        </div>

    )
}
}

export default Menu;
